
import type { FAQCategoryInfoList, CategoryListDetails, ICM20200211_RES } from "@/shared/type/icm202002sc00.type";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { defineComponent } from "vue";
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
  name: "icm202002sc00",

  components: {
    Pagination,
    BreadCrumb,
    SelectOption
  },

  data() {
    const dataTable = [] as FAQCategoryInfoList[];
    const tempTable = [] as FAQCategoryInfoList[];
    const header = [
      { columnName: this.$t('icm202002sc01.no') },
      { columnName: this.$t('icm202002sc01.view') },
      { columnName: this.$t('icm202002sc01.faq_category_code') },
      { columnName: this.$t('icm202002sc01.faq_category_name') },
      { columnName: this.$t('icm202002sc01.description') },
      { columnName: this.$t('Status') },
    ];
    return {
      dataTable,
      tempTable,
      header,
      searchValue: "",
      searchTypeCode: "",
      statusCode: '',
      totalItems: 0,
      currentPage: 1,
      pageSize: PAGE_SIZE,
      startingIndex: 1,
      isReset: false,
      breadcrumbs: [
        { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
        { value: 'icm202002sc00.contents_policy_management', keyPath: '', type: 'label' },
        { value: 'icm202002sc00.faq', keyPath: '', type: 'label' },
        { value: 'icm202002sc00.faq_category_list', keyPath: '', type: 'label' }
      ]
    };
  },
  mounted() {
    this.getCategoryInfoList();
  },
  // Methods 
  methods: {
    // Get Data List : ICM20200211 
    async getCategoryInfoList(mode?: string) {
      const body = {
        pageSize: this.pageSize,
        pageNumber: mode == 'search' ? 0 : this.currentPage,
        searchValue: this.searchValue,
        searchTypeCode: this.searchTypeCode,
        statusCode: this.statusCode
      };
      if (mode === 'search') {
        this.isReset = true;
        this.currentPage = 1
      }
      const responseData = await this.$request.getRequest("ICM20200211", body, false) as ICM20200211_RES;
      if (responseData.header.result) {
        const list = responseData?.body?.list.map((item: any) => {
          item["View"];
          item["FAQ Category Code"] = item.categoryCode;
          item["FAQ Category Name"] = item.categoryName;
          item["Description"] = item.categoryDesc;
          return item;
        });
        this.dataTable = list;
        this.tempTable = list;
        this.totalItems = responseData.body.totalCount;
      }
    },
    // Search 
    async handleSearch() {
      await this.getCategoryInfoList("search");
    },
    // Clear Search
    async handleReset() {
      this.isReset = true;
      this.currentPage = 1;
      this.searchTypeCode = "";
      this.searchValue = "";
      this.statusCode = ''
      await this.getCategoryInfoList();
    },
    // Move To Register 
    handleRegister() {
      this.$router.push({
        path: '/ICM202002SC01',
        query: {
          type: 'register'
        }
      })
      // this.$router.push("/integrated-content-management/faq-category/register");
    },
    // Clisk View 
    handleAction(index, rowData: CategoryListDetails) {
      this.$router.push({
        path: '/ICM202002SC01',
        query: {
          type: 'edit',
          categoryCode: rowData.categoryCode
        }
      })
      // this.$router.push(`/integrated-content-management/faq-category/edit/${rowData.categoryCode}`);
    },
    // Function Pagination
    handlePagination(pageNumber: number) {
      this.isReset = false
      this.currentPage = pageNumber
      this.getCategoryInfoList()
    },
    handleStartingIndex(startingIndex: number) {
      this.startingIndex = startingIndex
    }
  },
});
